/* Dashboard styles */
.dashboard {
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(44,62,80,0.08);
    padding: 32px 24px;
    margin: 24px 0;
}
.dashboard-header {
    margin-bottom: 24px;
}
.dashboard-header h1 {
    color: #2980b9;
    font-size: 2rem;
    margin: 0;
}
.dashboard-stats {
    display: flex;
    gap: 32px;
    margin-bottom: 32px;
}
.stat-item {
    background: #f7f7f7;
    border-radius: 6px;
    padding: 18px 28px;
    box-shadow: 0 1px 4px #e0e0e0;
    text-align: center;
    flex: 1;
}
.stat-item h2 {
    font-size: 1.1rem;
    color: #2c3e50;
    margin-bottom: 8px;
}
.stat-item p {
    font-size: 1.5rem;
    color: #2980b9;
    font-weight: bold;
    margin: 0;
}
.dashboard-summary {
    margin-top: 24px;
}
.dashboard-summary h2 {
    font-size: 1.2rem;
    color: #2c3e50;
    margin-bottom: 12px;
}
.dashboard-summary ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
.dashboard-summary li {
    background: #f4f6fa;
    border-radius: 4px;
    margin-bottom: 10px;
    padding: 10px 14px;
    box-shadow: 0 1px 2px #e0e0e0;
    font-size: 1rem;
}
@media (max-width: 900px) {
    .dashboard {
        padding: 12px 4px;
    }
    .dashboard-header h1 {
        font-size: 1.1rem;
    }
    .dashboard-stats {
        flex-direction: column;
        gap: 12px;
    }
    .stat-item {
        padding: 10px 8px;
    }
}
